.contener{
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    padding: 1rem 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px;
    width: 100%;
    margin-bottom: 20px;
    margin-top: 0;
    margin-right: 20px;
}
.image-holder{
    background-color: red;
    
    box-sizing: border-box;
    position: relative;
    display: block;
    user-select: none;
    overflow: hidden;
    width: 56px;
    min-width: 56px;
    height: 56px;
    border-radius: 32px;
    text-decoration: none;
    border: 0px;
    background-color: transparent;
    padding: 0px;
}
    

.title{
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
    -webkit-tap-highlight-color: transparent;
    color: inherit;
    font-size: 20px;
    line-height: 1.55;
    text-decoration: none;
    font-weight: 700;
    margin-bottom: 20px;
}
.botton-holder{
    display: flex;
    gap: 1rem;
}
.join{
    padding: 0px 18px;
    appearance: none;
    text-align: left;
    text-decoration: none;
    box-sizing: border-box;
    height: 36px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
    -webkit-tap-highlight-color: transparent;
    display: inline-block;
    width: auto;
    border-radius: 32px;
    font-weight: 600;
    position: relative;
    line-height: 1;
    font-size: 14px;
    user-select: none;
    cursor: pointer;
    border: 1px solid transparent;
    background-color: rgb(34, 139, 230);
    color: rgb(255, 255, 255);
}
.sign{
    padding: 0px 18px;
    appearance: none;
    text-align: left;
    text-decoration: none;
    box-sizing: border-box;
    height: 36px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
    -webkit-tap-highlight-color: transparent;
    display: inline-block;
    width: auto;
    border-radius: 32px;
    font-weight: 600;
    position: relative;
    line-height: 1;
    font-size: 14px;
    user-select: none;
    cursor: pointer;
    border: 1px solid rgb(34, 139, 230);
    background-color: transparent;
    color: rgb(34, 139, 230);
}